<template>
<div>
 <div id="photosphere"></div>
 <div>{{message}}</div>
</div>
 
</template>

<script>
import PhotoSphereViewer from 'photo-sphere-viewer'
export default {
  name: "App",
  data:function(){
    return {
      message:""
    }
  },
  mounted: function() {
    console.log(PhotoSphereViewer)
    var div = document.getElementById("photosphere");
    var PSV = new PhotoSphereViewer({
      panorama:
        "http://tassedecafe.org/wp-content/uploads/2013/01/parc-saint-pierre-amiens.jpg",
      container: div,
      time_anim: 3000,
      navbar: true,
      navbar_style: {
        backgroundColor: "rgba(58, 67, 77, 0.7)"
      }
    });
    PSV.startGyroscopeControl()
  }
};
</script>

<style>
#photosphere {
  background: #ddd;
  width: 350px;
  height: 600px;
}
</style>



